import { Text } from "@react-native-material/core";
import dayjs from "dayjs";
import { View } from "react-native";
import { layoutConstants } from "../../themes/layout-contants";

export default function TodayCalendar({textStyle, selectedDate = dayjs()}) {
  const month = selectedDate.format("MMM");
  const day = selectedDate.format("DD");
  const year = selectedDate.format("YYYY");

  return (
    <View style={{ display: "flex", flexDirection: "row", flex: 1 }}>
      <Text style={textStyle ?? textStyle} variant="h2" color={layoutConstants.textColorThird}>
        {day}
      </Text>
      <View style={{ margin: 5 }}>
        <Text style={textStyle ?? textStyle} variant="h5" color={layoutConstants.textColorThird}>
          {month}
        </Text>
        <Text style={textStyle ?? textStyle} variant="h5" color={layoutConstants.textColorThird}>
          {year}
        </Text>
      </View>
    </View>
  );
}
