import { Divider, IconButton, Text } from "@react-native-material/core";
import { useEffect, useState } from "react";
import { StyleSheet, TextInput, View } from "react-native";
import { layoutConstants } from "../../themes/layout-contants";
import BoxChildren from "../common/box-chidren";
import Icon from "@expo/vector-icons/MaterialCommunityIcons";
import InlineCalendar from "./inline-calendar";
import { componentConstants } from "../utils/component-contants";

export default function DailyPlanner({ styles, onSave, data, navigation, onDatePress, selectedDate }) {
  const [emotions, setEmotions] = useState([
    { icon: "excited", activeColor: "green", inactiveColor: layoutConstants.colorBlur },
    { icon: "happy", activeColor: "blue", inactiveColor: layoutConstants.colorBlur },
    { icon: "neutral", activeColor: "orange", inactiveColor: layoutConstants.colorBlur },
    { icon: "sad", activeColor: "red", inactiveColor: layoutConstants.colorBlur }
  ]);
  const setActiveEmotion = (activeEmotion) => {
    const newEmotions = emotions.map(emotion => {
      if (emotion.icon === activeEmotion.icon) {
        emotion.active = true;
      } else {
        emotion.active = false;
      }
      return emotion;
    });
    setEmotions(newEmotions);
  }
  const noteTypes = { Note: "Note", Priority: "Priority" };

  const [priority, setPriority] = useState([]);

  const [notes, setNotes] = useState([]);

  useEffect(() => {
    const savedPriority = data ? data.filter(n => n.type === noteTypes.Priority) : null;
    const savedNotes = data ? data.filter(n => n.type === noteTypes.Note) : null;
    savedNotes ? setNotes(savedNotes) : null;
    savedPriority ? setPriority(savedPriority) : null;
  }, [data]);

  const noteElement = (item, index) => {
    return <View key={index} style={{ display: "flex", flexDirection: "row" }}>
      <TextInput
        key={index}
        value={item.description}
        editable={false}
        style={{ color: layoutConstants.textColor, flex: 1, borderBottomWidth: 1, borderBottomColor: "#9E9E9E" }}
        placeholder={`${index + 1}`}
      />
      <IconButton
        onPress={() => {
          navigation.navigate(componentConstants.NOTE_DETAIL_SCREEN, { note: item, index });
        }}
        icon={(props) => <Icon name="pencil" {...props} color={"#616161"} />}
      />
    </View>
  }

  return (
    <View testID="daily-planner" style={styles ? styles?.container : defaultStyles.container}>
      <View>
        <View style={{ marginTop: 30, marginBottom: 5 }}>
          <InlineCalendar></InlineCalendar>
        </View>
        <Divider />
        <BoxChildren styles={{ container: {} }}>
          <View style={{ margin: 10, flexDirection: "row", alignItems: "center" }}>
            <Text variant="subtitle1">How do you feel?</Text>
            <View style={{ display: "flex", flexDirection: "row", justifyContent: "center" }}>
              {emotions.map((emotion) => {
                return (
                  <IconButton key={emotion.icon}
                    icon={(props) => (
                      <Icon name={`emoticon-${emotion.icon}-outline`} {...props} color={emotion.active ? emotion.activeColor : emotion.inactiveColor} size={30} />
                    )}
                    onPress={(event) => setActiveEmotion(emotion)}
                  />
                )
              })}
            </View>
          </View>
        </BoxChildren>
      </View>
      <View style={{ alignItems: "center", borderWidth: 2, marginHorizontal: 10, padding: 10, borderRadius: 20, borderColor: "#9E9E9E" }}>
        <BoxChildren styles={{ container: { width: "100%" } }}>
          <View style={{ width: "100%", padding: 5 }}>
            <Text>Priority</Text>
            {priority.map((p, index) => { return noteElement(p, index) })}
          </View>
        </BoxChildren>
        <BoxChildren styles={{ container: { width: "100%" } }}>
          <View style={{ width: "100%", padding: 5 }}>
            <Text>Notes</Text>
            {notes.map((n, index) => {
              return noteElement(n, priority.length + index);
            })}
          </View>
        </BoxChildren>
      </View>
    </View>
  );
}

const defaultStyles = StyleSheet.create({
  container: {
    borderTopStartRadius: 60,
    borderTopRightRadius: 60,
    backgroundColor: "#FAFAFA",
    borderTopWidth: 1,
    borderRightWidth: 1,
    borderLeftWidth: 1,
    flex: 1,
    borderColor: "#B0BEC5",
  },
});
