import { useDispatch } from "react-redux";
import { log } from "../components/utils/log";
import { constants } from "./constant";
import { getTodayObject, requestS3ByUrl, requestUrlS3 } from "./utils";
import { layoutConstants } from "../themes/layout-contants";
import dayjs from "dayjs";

export default class HomeService {
  dispatch;
  constructor() {
    this.dispatch = useDispatch();
  }

  async getDataByLink(link) {
    log.debug("getDataByLink request data");
    return await requestS3ByUrl(link);
  }

  async getCategories() {
    log.debug("getCategories request data");
    return await requestS3ByUrl("data/quotes/category.json");
  }

  async getBg() {
    log.debug("getBg request data");
    return await requestUrlS3(layoutConstants.HOME_BG_URLS[0]);
  }

  /**
   * 
   * @param {dayjs} date 
   * @returns 
   */
  async getDailyQuote(date) {
    log.debug("getDailyQuote");
    if(!date) {
      date = dayjs()
    }
    const formattedYearMonth = date.format("YYYYMM");
    const formattedYearMonthDate = date.format("YYYYMMDD");

    return await requestS3ByUrl(constants.BASE_DAILY_QUOTE_URL + formattedYearMonth + '/' + formattedYearMonthDate + '.json');
  }
}

let service;
const createHomeService = () => {
  if (service) {
    return service;
  }
  service = new HomeService();
  return service;
};
export { createHomeService };
