import uuid from 'react-native-uuid';

export default class NoteModel {
  id;
  description;
  date;
  type;
  createdDate;
  updatedDate;

  constructor(taskJson) {
    const {id, description, date, createdDate, updatedDate, type} = taskJson;
    this.id = id ? id : uuid.v4();
    this.description = description;
    this.date = date;
    this.type = type;
    this.createdDate = createdDate || new Date();
    this.updatedDate = updatedDate || new Date();
  }
}

export const noteTypes = { Note: "Note", Priority: "Priority" };
export const dateStoreFormat = "YY-MM-DD";
/**
 * 
 * @param {dayjs} selectedDate 
 * @returns 
 */
export const getWeekDays = (selectedDate) => {
  if(!selectedDate) return [];
  const stepDays = [-3, -2, -1, 0, 1, 2, 3];
  return stepDays.map((d) => {
    return selectedDate?.add(d, 'day');
  });
}