import { useIsFocused } from "@react-navigation/native";
import dayjs from "dayjs";
import { LinearGradient } from "expo-linear-gradient";
import React, { useContext, useEffect, useMemo, useState } from "react";
import { ImageBackground, StyleSheet, View } from "react-native";
import Swiper from "react-native-swiper/src";
import { connect, useDispatch } from "react-redux";
import DailyPlanner from "../components/daily/daily-planner";
import BoxLabel from "../components/home/box-label";
import TodayCalendar from "../components/home/today-calendar";
import BottomBarCustom from "../components/utils/bottom-bar-custom";
import { componentConstants } from "../components/utils/component-contants";
import { ViewLandscape } from "../components/utils/view-landscape";
import NoteModel, { dateStoreFormat, getWeekDays, noteTypes } from "../models/note/note-model";
import { ServicesContext } from "../services/providers/services-provider";
import { layoutConstants } from "../themes/layout-contants";
import { log } from "../components/utils/log";
import { setSelectedDate } from "../features/selected-date-slice";

/**
 * 
 * @param {navigation, dayjs} param0 
 * @returns 
 */
const DailyScreen = ({ navigation, selectedDate }) => {
  //Fixed warn useContext, useState
  const { homeService, noteService } = useContext(ServicesContext);
  const [dailyQuote, setDailyQuote] = React.useState(null);
  const [headerBgURL, setHeaderBgURL] = React.useState(null);
  const initNotes = [
    new NoteModel({ description: "", type: noteTypes.Priority }),
    new NoteModel({ description: "", type: noteTypes.Priority }),
    new NoteModel({ description: "", type: noteTypes.Note }),
    new NoteModel({ description: "", type: noteTypes.Note }),
    new NoteModel({ description: "", type: noteTypes.Note }),
  ]
  const [notes, setNotes] = React.useState(initNotes);
  const days = useMemo(() => {
    return getWeekDays(selectedDate);
  }, []);

  const dateString = useMemo(() => {
    return selectedDate.format(dateStoreFormat);
  }, [selectedDate]);

  const [selectedPosition, setSelectedPosition] = useState(3);

  const focus = useIsFocused();
  const dispatch = useDispatch();

  useEffect(() => {
    const getDailyQuoteSync = async () => {
      const dailyQuote = await homeService.getDailyQuote(selectedDate);
      setDailyQuote(dailyQuote);
    };
    getDailyQuoteSync();
    // selected position
    const diff = selectedDate.diff(days[3], "day");
    setSelectedPosition(3 + diff);
  }, [selectedDate]);

  useEffect(() => {
    const getBgSync = async () => {
      const url = await homeService.getBg();
      setHeaderBgURL(url);
    };
    getBgSync();
  }, []);

  useEffect(() => {
    const getNotesSync = async () => {
      let notes = await noteService.getNotesByDate(dateString);
      if (!notes) {
        noteService.saveNotesByDate(dateString, initNotes)
        notes = initNotes;
      }
      setNotes(notes);
    };
    focus && getNotesSync();
  }, [focus, dateString]);

  const onSave = async (event) => {
    await noteService.saveNotesByDate(dateString, event)
  }

  const onUserSwipe = (event) => {
    console.log(event);
    dispatch(setSelectedDate(days[event]));
  }
  return (
    <View style={{ flex: 1 }} testID='daily-screen'>
      <ViewLandscape>
        <LinearGradient colors={["#B0BEC5", "#CFD8DC", "white"]} style={{ flex: 1 }}>
          <ImageBackground
            source={{ uri: headerBgURL }}
            resizeMode='stretch'
            style={{ flex: 1 }}
          >
            <View testID='daily-screen-body' style={{ display: "block", height: "100%" }}>
              <Swiper onIndexChanged={onUserSwipe} index={selectedPosition} scrollViewStyle={{ height: 200 }} activeDotColor={layoutConstants.colorPrimary} loop={false} showsButtons={true}>
                {days.map((value, index) => {
                  return <View key={index}>
                    <View
                      testID='daily-screen-menu'
                      style={{
                        alignItems: "flex-end",
                        justifyContent: "flex-end",
                        height: 100,
                        flexDirection: "row",
                        paddingHorizontal: 20,
                        marginTop: 10
                      }}
                    >
                      <TodayCalendar textStyle={{ fontWeight: 500 }} selectedDate={value} />
                    </View>
                    <View testID="daily-screen-quote">
                      <BoxLabel
                        styles={{ box: styles.daily }}
                        label={dailyQuote?.content?.item}
                        textStyle={styles.textDaily}
                      ></BoxLabel>
                    </View>
                  </View>
                })}
              </Swiper>
              {/* <View style={{ padding: 20 }}>
                <UserMenu navigation={navigation} />
              </View> */}

              <View testID="daily-screen-planner" style={{ flex: 1 }}>
                <DailyPlanner data={notes} navigation={navigation} onSave={onSave} selectedDate={selectedDate} onDatePress={(event) => {
                  setDate(event)
                }} />
              </View>
            </View>
          </ImageBackground>
        </LinearGradient>
      </ViewLandscape>
      <BottomBarCustom page={componentConstants.DAILY_SCREEN} navigation={navigation}></BottomBarCustom>
    </View>
  );
};

const styles = StyleSheet.create({
  daily: {
    height: 100,
    padding: 5,
    color: layoutConstants.textColorSecondary,
  },
  textDaily: {
    fontWeight: layoutConstants.textBold,
    color: layoutConstants.textColorSecondary,
    fontSize: layoutConstants.quoteSize,
    fontStyle: "italic",
    paddingHorizontal: 22,
  },
  article: {
    height: 60,
    width: 300
  },
  textArticle: {
    fontWeight: layoutConstants.textBold,
    color: layoutConstants.textColor,
    paddingHorizontal: 10,
  },
});
const mapState = (state) => {
  const selectedDate = dayjs(state.selectedDate?.value);
  return {
    selectedDate,
  };
};
export default connect(mapState)(DailyScreen);
