import { createSlice } from '@reduxjs/toolkit'
import dayjs from 'dayjs';
import { log } from "utils/log";

export const selectedDateSlice = createSlice({
  name: 'selectedDate',
  initialState: {
    value: dayjs(),
  },
  reducers: {
    setSelectedDate: (state, action) => {
      log.debug("setSelectedDate", action);
      if(!action?.payload) {
        return;
      }
      state.value = action.payload;
    },
  },
});
// Selector
export const selectedWeekDays = (state) => {
  const stepDays = [-3, -2, -1, 0, 1, 2, 3];
  const selectedDate = dayjs(state?.selectedDate?.value);
  return stepDays.map((d) => {
    return selectedDate.add(d, 'day');
  });
}

export const { setSelectedDate } = selectedDateSlice.actions;

const selectedDateSliceReducer = selectedDateSlice.reducer;
export default selectedDateSliceReducer;