import { Text } from "@react-native-material/core";
import dayjs from "dayjs";
import { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { connect } from "react-redux";
import { getWeekDays } from "../../models/note/note-model";
import { layoutConstants } from "../../themes/layout-contants";

function InlineCalendar({ styles, selectedDate }) {
  const dayOfWeek = ["Sun", "Mon", "Tue", "Web", "Thu", "Fri", "Sat"];
  const date = selectedDate;
  const days = useMemo(() => {
    return getWeekDays(date);
  }, []);

  return (
    <View style={{ display: "flex", flexDirection: "row" }}>
      {days.map((d, index) => {
        const diff = date.diff(d, "day");
        return (
          <View key={index} style={[{ flex: 1, alignItems: "center" }, diff == 0 ? defaultStyles.containerActive: {}]}>
            <Text style={diff == 0 ? defaultStyles.active : defaultStyles.inactive}>{dayOfWeek[d.day()]}</Text>
              <Text style={diff == 0 ? defaultStyles.active : defaultStyles.inactive}>{d.format("DD")}</Text>
          </View>
        )
      })}
    </View >
  );
}
const defaultStyles = StyleSheet.create({
  container: {
    minWidth: 150,
    minHeight: 100,
    borderRadius: 10,
    margin: 5,
    padding: 10,
    backgroundColor: layoutConstants.bgColorSecondary,
  },
  containerActive: {
    borderBottomWidth: 2,
    borderColor: layoutConstants.colorPrimary
  },
  active: {
    color: layoutConstants.colorPrimary,
    fontWeight: 'bold'
  },
  inactive: {
    color: layoutConstants.colorBlur
  }
});

const mapState = (state) => {
  const selectedDate = dayjs(state.selectedDate?.value);
  return {
    selectedDate,
  };
};
export default connect(mapState)(InlineCalendar);


